import React from 'react'
import Header from 'components/Header.jsx'
import './style.styl'

export class App extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      title: <h1>App</h1>
    }
  }

  render () {
    return (
      <div className="mod-hub">
        <Header/>
        <div className="hub-body">
          <div className="hub-form">
            <div className="hub-desc">
              <textarea className="textarea-clean" placeholder="留下您的采购信息，如钢材品类，数量，交货期等，商家将在5分钟内跟您联系并给您报价，请保持手机通畅。"></textarea>
            </div>
          </div>
        </div>
      </div>
    )
  }
}



